<template>
  <div class="pem-steps" v-if="steps.length">
    <div class="pem-steps-line-wrapper">
      <div :style="'width:' + (currentStep * 100)/(steps.length - 1) + '%'"></div>
    </div>
    <div class="pem-steps-texts">
      <span :class="{'active': currentStep === index, ['pem-steps-text-' + index]: true}" v-for="(item, index) in steps" :key="item.desc">{{item.desc}}</span>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  props: {
    steps: {
      type: Array,
      default: []
    },
    currentStep: {
      type: [String, Number],
      default: 0
    }
  }
}
</script>



