<template>
  <div class="my-project full-screen">
    <div class="page-panel" :class="{white: !list.length}">
      <div v-if="!list.length" class="no-project">
        <div class="no-project-image"></div>
        <p>暂无项目信息</p>
      </div>
      <pem-icon name="create-project" @click="showDialog('create')" width="104" height="104"></pem-icon>
      <pem-scroll-view
        v-if="list.length"
        ref="scroll"
        class="scroll-view"
        :data="list"
        :pullup=true
        :pulldown=true
        @pullUpLoad="listMore"
        @pullDownRefresh="listRefresh">
        <div class="scroll-view-container">
          <div class="scroll-view-pulldown">下拉刷新...</div>
          <div class="my-project-item" v-for="(item, index) in list" :key="index" @click="showDialog('see')">
            <img class="my-project-item-logo" :src="item.logo" @error="defaultLogo"/>
            <div class="my-project-item-bar">
              <h1 class="my-project-item-title">
                <span>{{ item.name }}</span>
              </h1>
              <div class="my-project-item-info">
                <span v-if="item.turn">{{ item.turn }}</span>
                <span v-if="item.raisefunds">{{ item.currencytype }}{{ item.raisefunds }}万</span>
                <span v-if="item.address">{{ item.address }}</span>
              </div>
              <div class="my-project-item-state-wrapper">
                <span v-if="item.state === 'yfb' || item.state === 'shtg'" class="my-project-item-state my-project-can-deliver">可投递</span>
                <span class="my-project-item-state">{{ {
                    'dtj': '待提交',
                    'shz': '审核中',
                    'shwtg': '审核未通过',
                    'shtg': '审核通过',
                    'yfb': '已发布',
                    'yxx': '已下线'
                  }[item.state] }}</span>
              </div>
              <div class="my-project-item-steps">
                <pem-steps :steps="steps" :currentStep="{
                  'dtj': 0,
                  'shz': 1,
                  'shwtg': 0,
                  'shtg': 2,
                  'yfb': 3,
                  'yxx': 0
                }[item.state]"></pem-steps>
              </div>
            </div>
          </div>
          <div class="scroll-view-pullup">{{ isMore ? '加载中...' : '目前就这么多了' }}</div>
        </div>
      </pem-scroll-view>
    </div>
    <pem-operation-panel @backtotop="backtotop" bottom="436"/>
    <pem-loading v-show="isLoading"></pem-loading>
    <pem-dialog ref='dialog' :text="dialogTxt" confirmBtnText="我知道了" type="alert"></pem-dialog>
  </div>
</template>

<script type="text/ecmascript-6">
import PemOperationPanel from 'components/pem-operation-panel/pem-operation-panel'
import PemIcon from 'components/pem-icon/pem-icon'
import PemScrollView from 'components/scroll-view/scroll-view'
import PemSteps from 'components/steps/steps'
import PemDialog from 'components/dialog/dialog'
import PemLoading from 'components/pem-loading/pem-loading'
import { MiniSiteApi, ImgPath } from 'api'
import { setTitle } from 'common/js/util'

export default {
  components: {
    PemOperationPanel,
    PemScrollView,
    PemIcon,
    PemSteps,
    PemDialog,
    PemLoading
  },
  data () {
    return {
      currentPage: 1,
      pageSize: 10,
      dialogTxt: '',
      list: [],
      steps: [{
        desc: '提交'
      }, {
        desc: '审核'
      }, {
        desc: '通过'
      }, {
        desc: '发布'
      }],
      isMore: true,
      isLoading: true
    }
  },
  created () {
    this._initWXShare()
    this._setTitle('项目工场-发布项目')
    setTimeout(() => {
      this._initData()
    }, 20)
  },
  methods: {
    _initWXShare () {
      this.wxShare({
        title: '为您推荐项目工场',
        content: '我正在使用项目工场，有料的BP、有趣的VC都在这儿，一起来看项目吧！',
        linkUrl: window.location.origin
      })
    },
    _setTitle (title) {
      setTitle(title)
    },
    _initData () {
      this.getMyListNew('init')
    },
    backtotop () {
      this.$refs.scroll.scrollTo(0, 0, 300)
    },
    listMore () {
      if (!this.isLoading && this.isMore) {
        this.getMyListNew('more')
      }
    },
    listRefresh () {
      if (!this.isLoading) {
        this.isMore = true
        this.getMyListNew('init')
      }
    },
    getMyListNew (type = 'init') {
      this.isLoading = true
      if (type === 'init') {
        this.currentPage = 1
      }
      this._getMyListNew({
        currentPage: this.currentPage,
        pageSize: this.pageSize
      }, data => {
        this.isLoading = false
        if (+data.resultcode === 1) {
          this.currentPage = (+data.currentPage || 1) + 1
          if (+data.totalPage <= +data.currentPage) {
            this.isMore = false
          }
          let _list = (type === 'init') ? [] : this.list
          _list = _list.concat(data.resultList || []).map(item => {
            item.logo = item.logo ? (ImgPath + item.logo.replace(ImgPath, '')) : require('common/image/icon-project-Default_graph@2x.png')
            item.state = {
              100: 'dtj',
              200: 'shz',
              300: 'shtg',
              422: 'shwtg',
              510: 'yfb',
              511: 'yxx'
            }[+item.fstatus]
            return item
          })
          this.list = _list
        }
      })
    },
    showDialog (purpose) {
      this.dialogTxt = purpose === 'see' ? '您可以登录APP查看项目详情' : '您可以登录APP创建项目'
      this.$refs.dialog.show()
    },
    defaultLogo (e) {
      e.target.src = require('common/image/icon-project-Default_graph@2x.png')
    },
    _getMyListNew (options, callback) {
      MiniSiteApi.getMyListNew(options).then(data => {
        callback(data)
      })
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .my-project
    font-size: 0.24rem
    .no-project
      position: relative
      text-align: center
      .no-project-image
        bg-image(icon-my-project-Empty-state)
        background-size: 3.56rem 3.56rem
        width: 3.56rem
        height: 3.56rem
        margin: 0.64rem auto 0 auto
      p
        font-size: 0.32rem
        line-height: 0.34rem
        color: $color-text-title
  .icon-create-project
    position: fixed
    right: 0.2rem
    bottom: 5.48rem
    z-index: 1000
  .my-project-item
    padding: 0rem 0rem 0rem 1.52rem
    background: $color-panel
    position: relative
  .my-project-item-logo
    width: 0.92rem
    height: 0.92rem
    position: absolute
    top: 0.3rem
    left: 0.3rem
    border-radius: 0.08rem
  .my-project-item-bar
    position: relative
    padding: 0.3rem 0
    line-bottom()
    .my-project-item-title
      span
        color: $color-text
        font-size: 0.32rem
        font-weight: bold
        line-height: 0.44rem
        margin-right: 0.08rem
      i
        margin-right: 0.06rem

    .my-project-item-info
      margin-top: 0.1rem
      line-height: 0.34rem
      font-size: 0
      span
        font-size: 0.24rem
        padding: 0 0.1rem
        color: $color-text
        line-item-right-center()
        &:first-child
          padding-left: 0
        &:last-child
          padding-right: 0
          &:after
            display: none

    .my-project-item-state-wrapper
      position: absolute
      top: 0.3rem
      right: 0.3rem
      span
        display: inline-block
        font-size: 0.22rem
        line-height: 0.34rem
        color: $color-text
        background-color: #F0F0F0
        border-radius: 0.04rem
        padding: 0 0.06rem
        &.my-project-can-deliver
          margin-right: 0.16rem
          color: #fff
          background-image: linear-gradient(68deg, #FFA76A 4%, #FF8F41 100%)

    .my-project-item-title
    .my-project-item-info
    .my-project-item-state
      font-size: 0
      i, span
        vertical-align: middle
    .my-project-item-steps
      margin-right: 0.3rem
      margin-top: 0.3rem
</style>



